<template>
  <div>
    <div class="top" :style="rapidstyle"></div>

    <div class="rapidtop">
      <div v-for="item in listTop" :key="item.id">
        <div class="content">
          <img :src="item.id==1?rapidl:rapidr" />
          <div class="content-pro">
            <span>{{item.title}}</span>
            <span>{{item.subtitle}}</span>
          </div>
        </div>
      </div>
    </div>

    <div class="rapiddown">
      <div v-for="item in listDown" :key="item.id" class="content">
        <img :src="item.img_url" alt />
        <span>{{item.title}}</span>
      </div>
    </div>
    <hr />
    <div class="inform">
      <wipper :content="inform"></wipper>
    </div>
     <hr />
    <div class="activity">
      <wipper :content="imgs"></wipper>
    </div>
    <div class="cloud" >
     
    </div>
  </div>
</template>
<script>
import wipper from "../common/wipper";
export default {
  data() {
    return {
      listTop: [{id:1,title:"酱酱酱酱",subtitle:"海鲜拌饭鸡汁酱"},{id:2,title:"盐盐盐盐",subtitle:"喜马拉雅玫瑰盐"}],
      listDown: [{id:1,title:"法式可颂"},{id:2,title:"原味鸡"},{id:3,title:"冰拿铁"},{id:4,title:"雪顶奶茶"},{id:5,title:"鸡腿堡"}],

      rapidstyle:{backgroundImage:`url(${require("../../assets/topimg.png")}`} ,
      rapidl: require("../../assets/homepage/rapid-l.png"),
      rapidr: require("../../assets/homepage/rapid-r.png"),

      inform: {
        style: "inform",
        contentinform: [{id:1,title:"芋泥紫薯抹茶草莓凤梨朗姆葡萄"},{id:2,title:"半熟芝士冰山熔岩点心青梅玫瑰"}]
      },
      imgs: {
        style: "imgs",
        hastitle:true,
        contentimg: [
          { id: 1, img_url: require("../../assets/homepage/swiper-1.jpg") },
          { id: 2, img_url: require("../../assets/homepage/swiper-2.jpg") },
          { id: 3, img_url: require("../../assets/homepage/swiper-3.jpg") },
          { id: 4, img_url: require("../../assets/homepage/swiper-4.jpg") }
        ]
      }
    };
  },
  components: { wipper },
  created() {
    // this.axios
    //   .get("/rapidtop", {
    //     params: {}
    //   })
    //   .then(res => {
    //     if (res.status == 0) {
    //     } else {
    //      // this.listTop = res.data.list;
         
    //     }
    //   });
    // this.axios
    //   .get("/rapiddown", {
    //     params: {}
    //   })
    //   .then(res => {
    //     if (res.status == 0) {
    //     } else {
          let imgs = [
            require("../../assets/homepage/rapid-1.png"),
            require("../../assets/homepage/rapid-2.png"),
            require("../../assets/homepage/rapid-3.png"),
            require("../../assets/homepage/rapid-4.png"),
            require("../../assets/homepage/rapid-5.png")
          ];
          this.listDown =this.listDown.map((item, index) => {
            this.$set(item, "img_url", imgs[index]);
            return item;
          });
         
      //   }
      // });
    // this.axios
    //   .get("/inform", {
    //     params: {}
    //   })
    //   .then(res => {
    //     if (res.status == 0) {
    //     } else {
    //       this.inform.contentinform = res.data.list;
    //     }
    //   });
  }
};
</script>
<style lang="scss"  scoped>
.top {
  //background: url("~@/assets/topimg.png") no-repeat;
  background-size: 100% 100%;
  width: 100%;
  height: 120px;
}
.rapidtop {
  display: flex;
  justify-content: space-between;

  height: 75px;
  background-color: red($color: #000000);
  .content {
    border-radius: 5%;
    background-image: linear-gradient(#e66465, #9198e5);
    margin: 10px;
    height: 100%;
    display: flex;
    align-items: center;
    font-size: 12px;
    justify-content: center;
    .content-pro {
      text-align: left;
      padding-left: 10px;
    }
    img {
      width: 20%;
      height: 40%;
    }
  }
}
span {
  display: block;
}
.rapiddown {
  display: flex;
  margin-top: 30px;
  .content {
    width: 20%;
    font-size: 12px;
    img {
      width: 75%;
      height: 40px;
    }
  }
}
hr {
  height: 7px;
  background-color: #f2f3f5;
  border: none;
  margin: 13px -8px;
}
</style>


